export async function solveDay2(input) {
    let partA = 0;
    let partB = 0;

    const ids = input.split(',');
    ids = ids.split('-');

    ids.forEach(id => {
            const validID = validateIDs(id);
               validID ? partA++ : console.log('not a valid ID');
    })

    console.log('partA: ',partA);

    return { partA, partB };
}

const validateIDs = (id) => {
    console.log(id);
    return true;
}